@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merriweather|Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
.nav-bar {
  width: 100%;
  height: 50px;
  position: fixed;
  /* background-color: #fcfbf7; */
  background-color: white;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 999;
}

.left {
  flex: 20% 1;
  display: flex;
  padding: 5px;
}

.right {
  flex: 20% 1;
}

.search {
  margin-right: 5px;
  cursor: pointer;
}
.search-icon {
  cursor: pointer;
}

.center {
  flex: 60% 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
}

.nav-links {
  padding: 10px;
  cursor: pointer;
  font-family: "Titillium Web", sans-serif;
  text-decoration: none;
  color: black;
}

h4 {
  font-family: "Titillium Web", sans-serif;
  margin-left: 5px;
}

@media screen and (max-width: 900px) {
  .nav-bar {
    max-width: 80%;
  }
}

/* .app {
  background-color: #fcfbf7;
} */

@media screen and (max-width: 900px) {
  .app {
    width: 100%;
  }
}

.single-post-container {
  margin: 5% 6%;
}

.post-title {
  margin: auto;
  text-align: center;
  padding: 2%;
  padding-left: 30px;
  font-family: "Titillium Web", sans-serif;
  font-size: 24px;
}

.post-info {
  text-align: justify;
  flex-wrap: wrap;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.post-author {
  margin-bottom: 10px;
}

.delete-container {
  position: absolute;
  right: 80px;
  top: 295px;
}

.delete {
  text-align: left;
  cursor: pointer;
}

.create-post {
  padding-top: 7%;
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
}

.create-post label {
  text-align: left;
  display: block;
}

.create-post h2 {
  font-size: 25px;
  margin-bottom: 20px;
  font-family: "Titillium Web", sans-serif;
}

.create-post input,
.create-post textarea {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  display: block;
}
.create-post textarea {
  height: 100px;
}

.blog-button {
  width: 35%;
  background-color: #284b63;
  border: 1px solid grey;
  padding: 3px;
  border-radius: 4px;
  color: white;
  /* background: transparent; */

  cursor: pointer;
}

.blog-button:hover {
  color: #283618;
  border-color: #283618 1px solid;
  background-color: #fff;
}

.about-page {
  margin-top: 6%;
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  width: 95%;
  padding: 5px 7px;
}

.about-page-left {
  flex: 4% 1;
  margin-right: 15px;
  padding-right: 10px;
  margin-left: 35px;
}

.about-card {
  background: white;
  /* background-color: rgba(211, 204, 204, 0.123); */
  box-shadow: 0px 20px 30px #d9dbdf;
  padding: 10px;
  height: 95%;
}

.about-page-left h2 {
  font-family: "Titillium Web", sans-serif;
  text-align: center;
  margin: 10px 0;
}

.about-page-left h3 {
  font-family: "Titillium Web", sans-serif;
  padding: 0 10x;
  margin: 10px 0;
  text-align: center;
}

.about-info {
  padding: 10px 5px;
}

.about-page-right {
  flex: 5% 1;
  align-items: center;
  display: flex;
  justify-content: center;
}

.about-pic {
  max-width: 100%;
  height: 500px;
  border-radius: 8px;
  box-shadow: 0px 20px 30px #d9dbdf;
}

.about-icons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 150px;
}

.linkedin {
  color: #3166c2;
  border-style: none;
}

.github {
  color: black;
}

.twitter {
  color: #449bf0;
}

.form-container {
  height: 150px;
}

.email-form {
  display: flex;
  flex-direction: row;
}

.home-page {
  margin-top: 4%;
}

.posts-container {
  display: flex;
}

.header-component {
  margin-top: 60px;
  margin-bottom: 10px;
  width: 100%;
}

.header-title {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  position: absolute;
  top: 22%;
  font-size: 50px;
  font-family: "Titillium Web", sans-serif;
}

.header-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

@media screen and (max-width: 900px) {
  .header-component {
    width: 100%;
  }

  .header-image {
    width: 100%;
  }
}

.post-container {
  flex: 55% 1;
  width: 800px;
  display: flex;
  flex-direction: row-reverse;
  /* flex-wrap: wrap; */
  margin: 10px;
  justify-content: space-around;
}

.card {
  width: 700px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.square {
  width: 320px;
  height: 380px;
  background: white;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: 0px 20px 50px #d9dbdf;
  transition: all 0.3s ease;
  padding: 4px;
  margin: 10px;
}

.square:hover {
  -webkit-transform: translate(20px, -10px);
  transform: translate(10px, -10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
}

.card-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 7px;
  margin-bottom: 5px;
}

.card-title {
  margin: auto;
  margin-bottom: 5px;
  text-align: left;
  padding-top: 3px;
  padding-bottom: 5px;
  padding-left: 30px;
  font-family: "Merriweather", serif;
  font-size: 24px;
}

.card-info {
  text-align: justify;
  padding-left: 12px;
  padding-right: 15px;
  padding-bottom: 5px;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  color: #777171;
  line-height: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.card-button {
  background-color: #284b63;
  color: white;
  width: 60px;
  padding: 4px 10px;
  margin-right: 225px;
  margin-bottom: 5px;

  border-radius: 3px;
  text-decoration: none;
  font-size: 12px;
  cursor: pointer;
  font-family: "merriwheather";
  position: absolute;
}
.bottom-card-container {
  display: flex;
  justify-content: end;
  margin: 10px 5px;
}

.side-bar {
  flex: 13% 1;
  margin: 15px;
  padding-bottom: 25px;
  padding-right: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;

  /* background-color: blue; */
}

.sidebar-items {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  width: 85%;
}

.sidebar-image {
  max-width: 100%;
  height: auto;
  margin-top: 25px;
  margin-bottom: 5px;
}

.sidebar-title {
  padding-top: 7px;
  padding-bottom: 7px;
  width: 100%;
  border-top: 1px solid grey;
  border-bottom: 1px solid grey;
  line-height: 15px;
}

.list {
  margin-top: 5px;
  list-style: none;
}

.list-items {
  display: inline-block;
  width: 50%;
  margin-top: 10px;
  /* background-color: aliceblue; */
  cursor: pointer;
}

.sidebar-info {
  padding-bottom: 9px;
}

.sidebar-items > p {
  padding-top: 25px;
  padding-bottom: 25px;
}

.login-container {
  margin-top: 7%;
  margin-left: 6%;
  margin-right: 6%;
}

.login-form {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.login-header {
  font-family: "Titillium Web", sans-serif;
  font-size: 35px;
  color: #333333;
  line-height: 1.2;
  padding-bottom: 70px;
}

.login-input {
  border: none;
  font-size: 18px;
  color: #555555;
  height: 52px;
  background: transparent;
  padding: 0 5px;
  width: 40%;
  border-bottom-style: solid;
  border-bottom-color: #95adb6;
}

.login-button {
  background-color: #95adb6;
  border-radius: 25px;
  border: none;
  width: 100%;
  cursor: pointer;
  text-transform: uppercase;
  color: black;
  font-size: 16px;
  margin-top: 10px;
}

.button-container {
  width: 35%;
}

.register-container {
  margin-top: 3%;
}

.register-header {
  font-family: "Titillium Web", sans-serif;
  font-size: 35px;
  color: #333333;
  line-height: 1.2;
  padding-bottom: 30px;
}

